.prompt {
  font-family: 'Ubuntu+Mono', monospace;
  font-size: 3vw;
  word-break: keep-all;
  text-align: left;
  padding: 1rem;
  margin: 0;
  background: #1a1a1a;
  -webkit-box-shadow: 2px 1px 1px #424242, -1px 1px 1px #424242;
          box-shadow: 2px 1px 1px #424242, -1px 1px 1px #424242;
}

.prompt-alt {
  background: #eee;
  -webkit-box-shadow: 2px 1px 1px #bdbdbd, -1px 1px 1px #bdbdbd;
          box-shadow: 2px 1px 1px #bdbdbd, -1px 1px 1px #bdbdbd;
}

.prompt span {
  color: #eee;
}

.prompt-alt span {
  color: #212121;
}

@media (min-width: 640px) and (min-height: 640px) {
  .prompt {
    font-size: 1.5vw;
  }
}



