@import url('https://fonts.googleapis.com/css?family=Lato:400,700|Ubuntu+Mono');

body {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0;
  font-family: 'Lato', sans-serif;
  color: #7CB342;
  background: #212121;
}
.body {
}

.body-alt {
  color: #558B2F;
  background: #f2f2f2;
}

@media (min-width: 640px) and (min-height: 640px) {
  body {
    width: 80vw;
  }
}
pre, code {
  font-family: 'Ubuntu+Mono', monospace;
}



