.spinning {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

.next {
  position: fixed;
  width: 100vw;
  bottom: 0;
  text-align: center;
  text-decoration: none;
  padding: .5rem 0 1.5rem;
  font-size: 1.5rem;
  -webkit-box-shadow: -1px -1px 1px #424242, 2px -1px 1px #424242;
          box-shadow: -1px -1px 1px #424242, 2px -1px 1px #424242;
  background: #1a1a1a;
  color: #eee;
}

.next-alt {
  color: #212121;
  background: #eee;
  -webkit-box-shadow: -1px -1px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
          box-shadow: -1px -1px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
}

.next::before {
  content: "";
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #eee;
  position: absolute;
  top: 42px;
  left: calc(50vw - 4px);
}

.next-alt::before {
  border-top: 8px solid #212121;
}

.next:hover, .next:focus, .next:active {
  background: #000;
}

.next-alt:hover, .next-alt:focus, .next-alt:active {
  background: #bdbdbd;
}

@media (min-width: 640px) and (min-height: 640px) {
  .next {
    font-size: 1.5vw;
    left: 10vw;
    width: 80vw;
  }
  .next::before {
    top: calc(1.5vw + 1rem);
    left: calc(40vw - 8px);
  }
}
@media (min-width: 1750px) {
  .next {
    left: calc(50vw - 700px);
    width: 1400px;
  }
  .next::before {
    left: 692px;
  }
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



