.card {
  display: grid;
  grid-template-columns: 20% 80%;
  -webkit-box-shadow: 2px 2px 2px #424242, -1px -1px 1px #424242, -1px 2px 1px #424242, 2px -1px 1px #424242;
          box-shadow: 2px 2px 2px #424242, -1px -1px 1px #424242, -1px 2px 1px #424242, 2px -1px 1px #424242;
  margin: .5rem;
  cursor: pointer;
  background: #1a1a1a;
}

.card-alt {
  -webkit-box-shadow: 2px 2px 2px #bdbdbd, -1px -1px 1px #bdbdbd, -1px 2px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
          box-shadow: 2px 2px 2px #bdbdbd, -1px -1px 1px #bdbdbd, -1px 2px 1px #bdbdbd, 2px -1px 1px #bdbdbd;
}

.card:active, .card:hover, .card:focus {
  background: #000;
}

.card-alt:active, .card-alt:hover, .card-alt:focus {
  background: #fff;
}

.card h4, .card p {
  font-size: .8rem;
  margin: .5rem;
}

.img-holder {
  grid-row: 1 / 3;
  position: relative;
}

.img-holder img {
  max-width: 7vw;
  max-height: 7vw;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-filter: drop-shadow(.125vw -.125vw .25vw #bdbdbd) drop-shadow(-.125vw .125vw .25vw #bdbdbd);
          filter: drop-shadow(.125vw -.125vw .25vw #bdbdbd) drop-shadow(-.125vw .125vw .25vw #bdbdbd);
}

.locked {
  color: #EF9A9A;
  background: #333;
  cursor: auto;
}

.locked-alt {
  color: #E53935;
  background: #e2e2e2;
}

.locked:active, .locked:hover, .locked:focus {
  background: #333;
}

.locked-alt:active, .locked-alt:hover, .locked-alt:focus {
  background: #e2e2e2;
}

.future {
  color: #B39DDB;
  background: #333;
}

.future-alt {
  color: #8E24AA;
  background: #e2e2e2;
}

.future:active, .future:hover, .future:focus {
  background: #1a1a1a;
}

.future-alt:active, .future-alt:hover, .future-alt:focus {
  background: #f2f2f2;
}

@media (min-width: 640px) and (min-height: 640px) {
  .card:nth-of-type(2n+1) {
    margin-left: 2.5rem;
  }
}

@media (min-width: 640px) and (min-height: 640px) {
  .card h4, .card p {
    font-size: 1.2vw;
  }
  .card:nth-of-type(2n) {
    margin-right: 2.5rem;
  }
  .img-holder img {
    max-width: 4vw;
    max-height: 4vw;
  }
}



