.cards-holder {
  display: grid;
  grid-column-gap: 1vw;
  grid-row-gap: 1vw;
}

@media (min-width: 640px) {
  .cards-holder {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 639px) {
  .cards-holder {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 640px) {
  .cards-holder {
    margin-top: calc(50vh - 2.8vw - 9.5rem);
    grid-template-columns: 1fr 1fr;
  }
}



