.home {
  position: relative;
}

.profile {
  display: grid;
  grid-template-columns: 60% 40%;
}

.name {
  margin: calc(50vh - 4.5rem - 80px) auto 0;
  display: grid;
  grid-template-columns: 150px 1fr;
}

.name img {
  grid-row-start: 1;
  grid-row-end: 5;
  width: 112.5px;
  padding: 1rem;
  -webkit-filter: drop-shadow(-1px 1px 2px #bdbdbd) drop-shadow(2px -2px 3px #bdbdbd);
          filter: drop-shadow(-1px 1px 2px #bdbdbd) drop-shadow(2px -2px 3px #bdbdbd);
}

.name-alt img {
  -webkit-filter: drop-shadow(-1px 1px 2px #757575) drop-shadow(2px -2px 3px #757575);
          filter: drop-shadow(-1px 1px 2px #757575) drop-shadow(2px -2px 3px #757575);
}

.name h2, .name h3, .name p {
  line-height: 2.5rem;
  margin: 0;
}

.silhouette {
  background: no-repeat center/90% url("./silhouette_temp.png");
  height: calc(100vh - 8.25rem);
  grid-column-start: 2;
  grid-row: 1 / 3;
  -webkit-filter: drop-shadow(2px 1px 2px #424242) drop-shadow(0px 0px 1px #424242);
          filter: drop-shadow(2px 1px 2px #424242) drop-shadow(0px 0px 1px #424242);
}

.silhouette-alt {
  background: no-repeat center/90% url("./silhouette_temp_alt.png");
  -webkit-filter: drop-shadow(2px 1px 2px #bdbdbd) drop-shadow(0px 0px 1px #bdbdbd);
          filter: drop-shadow(2px 1px 2px #bdbdbd) drop-shadow(0px 0px 1px #bdbdbd);
}

@media (max-width: 639px) {
  .profile {
    grid-template-columns: 1fr;
  }
  .silhouette {
    display: none;
  }
  .name {
    grid-template-columns: 120px 1fr;
  }
  .name img {
    width: 90px;
  }
  .name h2, .name h3, .name p {
    line-height: 1.5rem;
  }
}

@media (min-width: 640px) and (min-height: 640px) {
  .name h2, .name h3, .name p {
    line-height: 2.5vw;
    font-size: 1.8vw;
  }
}

@media (min-width: 1750px) {
  .name {
    grid-template-columns: 240px 1fr;
  }
  .name img {
    width: 180px;
  }
}



